.menu {
  background-color: $colorGray;
  box-sizing: border-box;
  padding-top: $headerTopPadding;
  height: 100vh;
  overflow-y: auto;

  &__content {
    

    @include on1024px {
      padding-top: 48px;
    }
  }

  &__top {
    margin-bottom: 44px;
  }

  &__list {
    list-style: none;
    margin: 0 0 40px;
    padding: 0;
  }

  &__item {
    margin-bottom: 24px;

    &:last-child {
      margin-bottom: 0; 
    }


  }

  &__link {
    display: inline-block;
    font-family: 'Inter';
    font-weight: 700;
    color: #000;
    text-decoration: none; 
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px;
    text-transform: uppercase;
    padding: 8px;
    border-bottom: 1px solid transparent;
    transition: border-color 0.3s;

    &:hover {
      border-bottom: 1px solid #000;
    }
  }

  &__call {

    display: block;
    width: max-content;
    font-family: Inter;
    font-weight: 700;
    text-transform: uppercase;
    color: #1D1D1D;
    text-decoration: none;
    

    &--phone {
      font-size: 16px;
      line-height: 27px;
      margin-bottom: 16px;
    }
  
    &--action {
      font-size: 12px;
      letter-spacing: 2px;
      line-height: 16px;
      padding-bottom: 8px;
      border-bottom: 1px solid #000;
    }
  }

  }
