.form-field {
  box-sizing: border-box;
  display: block;
  width: 100%;
  height: 48px;

  font-size: 14px;
  font-family: Poppins;
  font-weight: 400;
  letter-spacing: 0px;
  background-color: #F8F8FA;


  border-radius: 40px;
  border: 1px solid transparent;
  padding: 14px 16px;

  

  &:hover {
    box-sizing: border-box;
    border: 1px solid #D5D7DE;

  }

  &:hover::placeholder {
    color: black;
  }

  &:focus {
    box-sizing: border-box;
    border: 1px solid black;
    outline: none;
  }

  &::placeholder {
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0px;
    color: #7E7E83;
  }

  &--textarea {
    height: 148px;
    font-family: inherit;
    resize: none;
    border-radius: 16px;
  }
}
