.header {
  background-image: url(/images/header-background.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;

  .header__content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    box-sizing: border-box;
    height: 100vh;
    padding: 28px 0 88px 0;

    @include on744px {
      padding: 36px 0 130px 0;
    }

    @include on1024px {
      padding: 36px 0 122px 0;
  }
  }
}

.header__nav-left {
  

  .logo__image {
    grid-area: navleft;
    display: grid;
    align-content: center;
  }
}

.header__nav-right {
  --icon-width: 18px;

  display: grid;
  grid-area: navright;
  grid-template-columns: var(--icon-width) var(--icon-width);
  gap: 30px;
  justify-content: flex-end;

  @include on744px {
    --icon-width: 24px;
  }

  @include on1024px {
    gap: 40px;
    --icon-width: 24px;
  }
}



.header__title {
  font-family: 'Poppins', sans-serif;
  font-weight: 700;
  font-size: 36px;
  font-weight: bold;
  line-height: 36px;
  letter-spacing: -1.5px;
  margin: 0;
  text-align: center;

  @include on744px {
    font-size: 64px;
    line-height: 64px;
  }

  @include on1024px {
    font-size: 80px;
    line-height: 80px;
  }
}


.header__call {
  
  text-decoration: none;

  @include on1024px {

    &::before {
      display: block;
      width: max-content;
      content: "+1 234 555-55-55";
      position: relative;
      top: 90%;
      transform: translate(-100%, -50%);
      color: #292929;
      font-family: 'Poppins';
      font-weight: 700;
      font-size: 12px;
      line-height: 16px;
      letter-spacing: 2px;
      text-transform: uppercase;
      opacity: 0;
      padding-right: 20px;
      pointer-events: none;
    }
}

&:hover::before {
  opacity: 1;
  pointer-events: all;
  }
}
