.about {
  font-family: Poppins;
  color: #FFFFFF;
  

  &__title {
    font-style: normal;
    font-weight: 500;
    font-size: 32px;
    line-height: 100%;
    letter-spacing: -1.5px;
    margin-bottom: 32px;

    @include on744px {
      font-size: 48px;
      line-height: 48px;
      text-align: left;
    }

    @include on1024px {
      font-size: 64px;
      line-height: 64px;
      text-align: left;
      // margin-bottom: 0;
      }
  }

  &__paragraph {

    font-family: 'Inter';
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    margin-top: 0;
    // margin-bottom: 80px;

    @include on744px {
      font-size: 24px;
      line-height: 140%;
    }

    

    @include on1024px {
    font-family: Inter;
    font-size: 24px;
    font-weight: 500;
    line-height: 34px;
    letter-spacing: 0px;
    text-align: left;
    }
  }

}