.contacts {
  &__info {
    margin: 0 0 24px;
     
    &--first {
      margin-top: 48px;

      @include on744px {
        margin-top: 0;
      }

      @include on1024px {
        margin-top: 0;
      }
    }
  }

  &__info-title {
    margin: 0 0 8px;
    font-family: Poppins;
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0px;
    text-align: left;

  }

  &__info-link {
    font-size: 16px;
    font-weight: 500;
    line-height: 22px;
    text-align: left;
    color: inherit;
    text-decoration: none;
  }

  &__field {
    margin: 0 0 16px;

    &--last {
      margin: 0 0 32px;
    }
  }
}