.place {

  &__photos {
    margin-bottom: 16px;
    
  }

  &__photo-container {
    position: relative;
    width: 100%;
    box-sizing: border-box;
    max-height: 321px;
    overflow: hidden;
    transition: 1s;
    position: relative;
   
    &:hover {
      transform: scale(1.2);
      z-index: 1;
    }


    &::after {
      content: "";
      display: block;
      padding-bottom: 100%;
 
    }

    @include on744px {
      height: 187px;
      padding-bottom: 0;
    }

    @include on1024px {
      height: 321px;
      padding-bottom: 0;
    }
  }

  &__photo {
    position: absolute;
    width: 100%;
    height: 100%;

    object-fit: cover;
    object-position: center;
    transition: 1s;

    &:hover {
      // transform: scale(1.3);
    }
  }

  &__title {
    color: white;
    font-family: Poppins;
    font-size: 24px;
    font-weight: 700;
    line-height: 34px;
    letter-spacing: 0px;
    text-align: left;
  }
 
  &__description {
    margin-top: 8px;
    line-height: 22px;
    letter-spacing: 0px;
    text-align: left;
  }
}