p, h1, h2, h3, h4 {
  margin: 0;
}

.compare-bikes {
  &__item {
    margin-bottom: 32px;

    &:last-child {
      margin-bottom: 0;
    }

    @include on744px {
      margin-bottom: 0;
    }

    @include on1024px {
      margin-bottom: 0;
    }
  }
}

.bike {
  margin-bottom: 40px;


  @include on744px {
    margin-bottom: 48px;
  }

  @include on1024px {
    margin-bottom: 0;
  }

  &__image-container {
    margin-bottom: 32px;
  }

  &__image {

    width: 100%;
    grid-column: 1 / 3;
    transition: 1s;
    cursor: pointer;
    position: relative;

    @include on744px {
      grid-column: 2 / 6;
    }

    @include on1024px {
      grid-column: 1 / 5;
    }

    &:hover {
      transform: scale(1.2);
      z-index: 1;
    }
  }

  &__model {

    font-family: 'Poppins';
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 140%;
    color: #fff;
    margin-bottom: 8px;
  }

  &__description {

    font-family: Poppins;
    font-size: 16px;
    font-weight: 400;
    line-height: 22px;
    letter-spacing: 0px;
    text-align: left;
    color: #fff;
    margin-bottom: 16px;
  }

  &__price {

    font-family: Poppins;
    font-size: 24px;
    font-weight: 700;
    line-height: 34px;
    letter-spacing: 0px;
    text-align: left;
    color: #fff;
  }
}

.link {
  text-decoration: none;
  color: white;
}
