@import "../utils/_vars.scss";

.page {
  scroll-behavior: smooth;
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  color: #FFFFFF;

  &__body {
  background: #1d1d1d;
  margin: 0;


  &--with-menu {
    overflow: hidden;
  }
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transform: translateX(-100%);
    transition: transform 0.3s;

    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    margin-bottom: 60px;

    @include on744px {
      margin-bottom: 120px;
    }

    @include on1024px {
      margin-bottom: 160px;
    }
  }
 
  &__section-title {
    margin-bottom: 32px;
    // margin-top: 20px;
    font-family: Poppins;
    font-size: 32px;
    font-weight: 500;
    line-height: 32px;
    letter-spacing: -1.5px;
    color: #FFFFFF;
    text-align: center;
    margin-bottom: 48px;

    @include on744px {
      font-size: 48px;
      margin-bottom: 64px;
    }

    @include on1024px {
      font-size: 64px;
    }
  }
}