.button {
  background-color: white;
  border-radius: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  width: 100%;
  height: 56px;
  text-decoration: none;
  cursor: pointer;

  color: black;
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 27px;
  transition: all 0.3s;

  &--primary {
    box-shadow: 1px 4px 16px rgba(232, 232, 240, 0.4);
    border-radius: 40px;
  }

  &:hover {
    box-shadow: 1px 4px 16px rgba(232, 232, 240, 0.4);
  }

  &:active {
    box-shadow: 1px 4px 16px rgba(232, 232, 240, 0.64);
  }
}